import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import { deepClone } from '@/until';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.showEditbox = false;
        this.showAddNav = false;
        this.pageTitle = '抽成设置';
        this.typeoptions = [
            {
                label: '固定',
                value: 'regular'
            },
            {
                label: '按类型',
                value: 'bytype'
            }
        ];
        this.data = [];
        this.currentIndex = 0;
        this.modelIndex = 0;
        this.headImgList = [];
        this.model = {
            drawmode: 'ladder'
        };
        this.imageList = [];
        this.isEdit = false;
        this.fields = [
            {
                type: 'radio-group',
                label: '抽成方式',
                modelKey: 'drawmode',
                props: {
                    options: [
                        {
                            label: '阶梯收费',
                            value: 'ladder'
                        },
                        {
                            label: '固定收费',
                            value: 'fixed'
                        },
                        {
                            label: '百分比',
                            value: 'percentage'
                        }
                    ]
                },
                rules: {
                    required: true
                },
                messages: {
                    required: '必须选择一种抽成方式'
                }
            }
        ];
        this.ladder = [];
        this.fixed = {
            type: '',
            value: 0,
            typeValue: {
                'delivery': 0,
                'selflifting': 0
            }
        };
        this.percentage = {
            type: '',
            value: 0,
            typeValue: {
                'delivery': 0,
                'selflifting': 0
            }
        };
        this.bisection = {
            number: 0,
            fee: 0
        };
        this.savefields = [
            {
                type: 'submit',
                label: '保存'
            }
        ];
        this.options = {
            scrollToInvalidField: true,
            layout: 'classic' // classic fresh
        };
    }
    get shopId() {
        return this.$route.params.id;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    typeOptionsChange(index, type) {
        if (this.model.drawmode === 'ladder') {
            if (this[this.model.drawmode][index].type === 'bytype') {
                if (!this[this.model.drawmode][index].typefee) {
                    this[this.model.drawmode][index].typefee = {
                        'delivery': 0,
                        'selflifting': 0
                    };
                }
            }
        }
        else {
            if (this[this.model.drawmode].type === 'bytype') {
                if (!this[this.model.drawmode].typeValue) {
                    this[this.model.drawmode].typeValue = {
                        'delivery': 0,
                        'selflifting': 0
                    };
                }
            }
        }
    }
    created() {
        let shopData = deepClone(this.$route.params);
        this.pageTitle = shopData.title;
        this.initConfig(shopData);
        this.headImgList = [shopData.headImg];
    }
    addLadder() {
        this.ladder.push({
            section: [0, 0],
            fee: 0,
            type: 'regular'
        });
    }
    // ----------------计算属性-----------------------
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    // ----------------监听方法-----------------------
    // ----------------调用方法-----------------------
    resetHandler() {
        this.$emit('cancel');
    }
    submitHandler(e) {
        e.preventDefault();
        // let setData = {...this.model, headImg: this.headImgList[0] || '"http://mmbiz.qpic.cn/mmbiz_jpg/5XHic5LQPCDh6os0mLo4IgYAfsHUicgR1ic3RsGfo19PkzF5ictOueZ9ktuOyuC4XbwAADNbrMibjuicy1JYwojhG36w/0"'}
        // this.$store.dispatch('updateShopInfoByObj', {
        //   shopId: this.shopId,
        //   value: setData
        // }).then((e) => {
        //   if (e.success) {
        //     this.$createDialog({
        //       type: 'alert',
        //       content: '保存成功',
        //       onConfirm: (e) => {
        //         this.$router.push({
        //           name: 'businessManagement',
        //           params: {
        //             id: this.shopId,
        //             oprate: 'update',
        //             data: setData
        //           }
        //         })
        //       }
        //     }).show()
        //   }
        // })
        let currentType = this.model.drawmode;
        let setData = {
            type: currentType,
            value: null
        };
        if (currentType === 'ladder') {
            // 阶梯收费
            let state = this.ladderValidate();
            if (!state) {
                return;
            }
        }
        else if (currentType === 'fixed') {
            // 固定收费
            this.fixed.value = Number(this.fixed.value);
            if (0 > this.fixed.value || isNaN(this.fixed.value)) {
                const toast = this.$createToast({
                    txt: '固定值需大于等于0',
                    type: 'error'
                });
                toast.show();
                return;
            }
        }
        else if (currentType === 'percentage') {
            // 百分比
            this.percentage.value = Number(this.percentage.value);
            console.log(typeof this.percentage.value, 'this.percentage.valueppp');
            if (0 > this.percentage.value || this.percentage.value > 100 || isNaN(this.percentage.value)) {
                const toast = this.$createToast({
                    txt: '百分值需大于等于0小于等于100',
                    type: 'error'
                });
                toast.show();
                return;
            }
        }
        else if (currentType === 'bisection') {
            // 等分
            if (this.bisection.number <= 0) {
                const toast = this.$createToast({
                    txt: '等分值需大于0',
                    type: 'error'
                });
                toast.show();
                return;
            }
            this.bisection.fee = Number(this.bisection.fee);
            if (0 > this.bisection.fee || isNaN(this.bisection.fee)) {
                const toast = this.$createToast({
                    txt: '等分抽成需大于等于0',
                    type: 'error'
                });
                toast.show();
                return;
            }
        }
        setData.value = this[currentType];
        let updateData = {
            dfConfig: setData
        };
        this.$store.dispatch('updateShopInfoByObj', {
            shopId: this.shopId,
            value: {
                dfConfig: setData
            }
        }).then((e) => {
            if (e.success) {
                this.$createDialog({
                    type: 'alert',
                    content: '保存成功',
                    onConfirm: (e) => {
                        this.$router.push({
                            name: 'businessManagement',
                            params: {
                                id: this.shopId,
                                oprate: 'update',
                                data: updateData
                            }
                        });
                    }
                }).show();
            }
        });
    }
    ladderValidate() {
        this.ladder = this.ladder.map((item) => {
            return {
                section: [isNaN(Number(item.section[0])) ? 0 : Number(item.section[0]), isNaN(Number(item.section[1])) ? 0 : Number(item.section[1])],
                fee: isNaN(Number(item.fee)) ? 0 : Number(item.fee),
                type: item.type || '',
                typefee: item.typefee
            };
        });
        let state = true;
        if (this.ladder.length <= 0) {
            state = false;
            const toast = this.$createToast({
                txt: '请设置阶梯',
                type: 'error'
            });
            toast.show();
        }
        else {
            let currentsection;
            this.ladder.some((item) => {
                if (!currentsection) {
                    // 起始
                    currentsection = item.section;
                    if (currentsection[0] !== 0) {
                        state = false;
                        const toast = this.$createToast({
                            txt: '阶梯收费需要从0开始',
                            type: 'error'
                        });
                        toast.show();
                        return true;
                    }
                }
                else {
                    if (item.section[0] !== currentsection[1]) {
                        state = false;
                        const toast = this.$createToast({
                            txt: '后一阶梯最小值需等于前一个阶梯最大值',
                            type: 'error'
                        });
                        toast.show();
                        return true;
                    }
                    else {
                        currentsection = item.section;
                    }
                }
                if (currentsection[1] <= currentsection[0]) {
                    state = false;
                    const toast = this.$createToast({
                        txt: '阶梯收费结束费用需大于起始费用',
                        type: 'error'
                    });
                    toast.show();
                    return true;
                }
            });
        }
        return state;
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    deleteLadder(index) {
        this.ladder.splice(index, 1);
    }
    initConfig(shopData) {
        let type = '';
        if (shopData && shopData.dfConfig) {
            type = shopData.dfConfig.type;
            this[type] = shopData.dfConfig.value;
        }
        else {
            type = 'ladder';
        }
        this.model = {
            drawmode: type
        };
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox
        }
    })
], businessManagement);
export default businessManagement;
